<template>
  <!--æŽ¥å£åˆ—è¡¨-->
  <div class="role-manage">
    <el-button
            type="primary"
            @click.native.prevent="showDialog('create')"
            icon="el-icon-plus">
      æ–°å»ºæŽ¥å£
    </el-button>
    <el-table
            :data="tableData"
            style="width: 100%">
      <el-table-column
              prop="operation_id"
              label="NO.">
      </el-table-column>
      <el-table-column
              prop="operation_name"
              label="æŽ¥å£åç§°">
      </el-table-column>
      <el-table-column
              prop="router"
              label="æŽ¥å£è·¯ç”±">
      </el-table-column>
      <el-table-column
              prop="check_param"
              label="æŽ¥å£å‚æ•°">
      </el-table-column>
      <el-table-column
              prop="content"
              label="æè¿°">
      </el-table-column>
      <el-table-column
              label="æ“ä½œ">
        <template slot-scope="scope">
          <el-button
                  @click.native.prevent="showDialog('edit', scope.row)"
                  type="text"
                  size="small">
            ç¼–è¾‘
          </el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
            @size-change="handleSizeChange"
            @current-change="changePage"
            :current-page.sync="page"
            :page-sizes="[20, 50, 100, 200]"
            :page-size="pageSize"
            layout="total, sizes, prev, pager, next"
            prev-text="ä¸Šä¸€é¡µ"
            next-text="ä¸‹ä¸€é¡µ"
            :total="totalCount">
    </el-pagination>
    <el-dialog
            :title="dialogTitle"
            :visible.sync="show"
            width="30%">
      <el-form
              :model="formParams"
              :rules="rules"
              ref="operation"
              label-width="100px">
        <el-form-item
                class="is-required"
                prop="operationName"
                label="æŽ¥å£åç§°">
          <el-input v-model="formParams.operationName"></el-input>
        </el-form-item>
        <el-form-item
                class="is-required"
                prop="operationRouter"
                label="æŽ¥å£è·¯ç”±">
          <el-input v-model="formParams.operationRouter"></el-input>
        </el-form-item>
        <el-form-item label="æŽ¥å£å‚æ•°">
          <el-input v-model="formParams.operationParam"></el-input>
        </el-form-item>
        <el-form-item label="æè¿°">
          <el-input v-model="formParams.operationContent"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="show = false">å– æ¶ˆ</el-button>
        <el-button
                v-show="dialogTitle === 'æ–°å»ºæŽ¥å£'"
                type="primary"
                @click="save('create')">
          ç¡® å®š
        </el-button>
        <el-button
                v-show="dialogTitle === 'ç¼–è¾‘æŽ¥å£'"
                type="primary"
                @click="save('edit')">
          ç¡® å®š
        </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
  import { getInterfaceList, createOperation, updateOperation } from '../../api/api'

  export default {
    data () {
      return {
        // tableæ‰€æœ‰æ•°æ®
        tableData: [],
        // å½“å‰ç¿»é¡µæ•°
        page: 1,
        // æ€»é¡µæ•°
        totalPage: 3,
        // tableæ€»æ¡æ•°
        totalCount: 1,
        // æ¯é¡µæ˜¾ç¤ºå¤šå°‘æ¡
        pageSize: 20,
        show: false,
        dialogTitle: '',
        formParams: {
          operationId: '',
          // æŽ¥å£åç§°
          operationName: '',
          // æŽ¥å£è·¯ç”±
          operationRouter: '',
          // æŽ¥å£å‚æ•°
          operationParam: '',
          // æè¿°
          operationContent: '',
        },
        rules: {
          operationName: [{
            required: true, message: 'æŽ¥å£åç§°ä¸èƒ½ä¸ºç©º'
          }],
          operationRouter: [{
            required: true, message: 'æŽ¥å£è·¯ç”±ä¸èƒ½ä¸ºç©º'
          }]
        }
      }
    },
    mounted () {
      this.getOperationList(1, 20)
    },
    methods: {
      /**
       * èŽ·å–æŽ¥å£åˆ—è¡¨
       * @param {Number} page é¡µç 
       */
      getOperationList (page, pageSize) {
        this.page = page
        this.pageSize = pageSize
        let params = {
          page_size: pageSize,
          page_num: page
        }
        getInterfaceList(params).then(res => {
          if (res.data.error_no === 0) {
            this.tableData = res.data.result.operations
            this.totalCount = parseInt(res.data.result.total_count)
          } else {
            this.$message.error(res.data.error_msg)
          }
        })
          .catch(error => {
            this.$message.error('æœåŠ¡å™¨å‚²å¨‡äº†ï¼Œè¯·åˆ·æ–°é‡è¯•')
          })
      },
      changePage (val) {
        this.getOperationList(val, this.pageSize)
      },
      handleSizeChange (val) {
        this.getOperationList(1, val)
      },
      /**
       * æ–°å»ºã€ç¼–è¾‘æŽ¥å£
       * @param {String} operation æ“ä½œï¼ˆç¼–è¾‘ or åˆ é™¤ï¼‰
       * @param {Object} data
       */
      showDialog (operation, data = {}) {
        this.show = true
        if (operation ===  'create') {
          this.$nextTick(() => {
            this.$refs['operation'].resetFields()
            this.formParams.operationName = ''
            this.formParams.operationRouter = ''
            this.formParams.operationParam = ''
            this.formParams.operationContent = ''
            this.formParams.operationId = ''
          })
          this.dialogTitle = 'æ–°å»ºæŽ¥å£'
        } else {
          this.formParams.operationName = data.operation_name || ''
          this.formParams.operationRouter = data.router || ''
          this.formParams.operationParam = data.check_param || ''
          this.formParams.operationContent = data.content || ''
          this.formParams.operationId = data.operation_id || ''
          this.dialogTitle = 'ç¼–è¾‘æŽ¥å£'
        }
      },
      /**
       * ä¿å­˜
       * @param {String} operation æ“ä½œï¼ˆç¼–è¾‘ or åˆ é™¤ï¼‰
       */
      save (operation) {
        this.formParams.operationName = this.formParams.operationName.trim()
        this.formParams.operationRouter = this.formParams.operationRouter.trim()
        this.$refs['operation'].validate((valid) => {
          if (valid) {
            let params = {
              operation_name: this.formParams.operationName,
              content: this.formParams.operationContent,
              router: this.formParams.operationRouter,
              check_param: this.formParams.operationParam
            }
            if (operation === 'create') {
              createOperation(params).then(res => {
                if (res.data.error_no === 0) {
                  this.$message.success('ä¿å­˜æˆåŠŸ')
                  this.getOperationList(1, this.pageSize)
                  this.show = false
                } else {
                  this.$message.error(res.data.error_msg)
                }
              })
                .catch(() => {
                  this.$message.error('æœåŠ¡å™¨å‚²å¨‡äº†ï¼Œè¯·åˆ·æ–°é‡è¯•')
                })
            } else {
              params.operation_id = this.formParams.operationId
              updateOperation(params).then(res => {
                if (res.data.error_no === 0) {
                  this.$message.success('ä¿å­˜æˆåŠŸ')
                  this.show = false
                  this.getOperationList(this.page, this.pageSize)
                } else {
                  this.$message.error(res.data.error_msg)
                }
              })
                .catch(() => {
                  this.$message.error('æœåŠ¡å™¨å‚²å¨‡äº†ï¼Œè¯·åˆ·æ–°é‡è¯•')
                })
            }
          }
        })
      }
    }
  }
</script>



