<template>
  <div class="check-all">
    <span class="set-name">{{allRenderData.set_name + 'ï¼š'}}</span>
    <el-checkbox
      :indeterminate="isIndeterminate"
      v-model="checkAll"
      @change="handleCheckAllChange">
      å…¨é€‰
    </el-checkbox>
    <el-checkbox-group
      v-model="checkData"
      @change="handleCheckedChange">
      <el-checkbox
        v-for="item in allRenderData.permissions"
        :label="item.permission_id"
        :key="item.permission_id">
        {{item.permission_name}}
      </el-checkbox>
    </el-checkbox-group>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        // æ˜¯å¦å…¨é€‰çŠ¶æ€åˆ¤æ–­ï¼Œboolean
        checkAll: false,
        // æ˜¯å¦åŠé€‰çŠ¶æ€åˆ¤æ–­ï¼Œboolean
        isIndeterminate: false,
        // æ‰€æœ‰é€‰ä¸­çš„id
        checkData: this.checkedDataFromProps
      }
    },
    watch:{
      // é€‰ä¸­çš„æ•°æ®æœ‰æ›´æ”¹æ—¶ï¼Œä¿®æ”¹props
      checkData: function(newval) {
        this.checkData = newval
        this.$emit('update:checkedDataFromProps', this.checkData)
      }
    },
    props: {
      // ä»Žçˆ¶å…ƒç´ ä¼ è¿‡æ¥çš„ï¼Œå·²ç»é€‰ä¸­çš„å…ƒç´ 
      checkedDataFromProps: {
        type: Array,
        default: []
      },
      // æ‰€æœ‰æ¸²æŸ“æ•°æ®
      allRenderData: {
        type: Object,
        default: {}
      },
      // æ¸²æŸ“æ•°æ®ä¸­æŠ½å‡ºidéƒ¨åˆ†ï¼Œæ–¹ä¾¿åšå…¨é€‰åˆ¤æ–­
      allCheckData: {
        type: Array,
        default: []
      }
    },
    mounted() {
      // vueå£°æ˜Žå‘¨æœŸé—®é¢˜ï¼Œä¸´æ—¶å¤„ç†ï¼Œæœªæ¥å†ç ”ç©¶
      setTimeout(() => {
        this.checkData = this.checkedDataFromProps
        this.checkAll = this.checkData.length == this.allCheckData.length
        this.isIndeterminate = this.checkData.length < this.allCheckData.length && this.checkData.length > 0
      }, 100)
    },
    methods: {
      // å…¨é€‰æŒ‰é’®ç‚¹å‡»äº‹ä»¶
      handleCheckAllChange(val) {
        // åˆ¤æ–­æ˜¯å¦å…¨é€‰
        this.checkData = val ? this.allCheckData : [];
        this.isIndeterminate = false;
      },
      // æŸä¸ªcheckboxç‚¹å‡»äº‹ä»¶
      handleCheckedChange(value) {
        // èŽ·å–é€‰ä¸­çš„æ•°é‡
        let checkedCount = value.length;
        // åˆ¤æ–­æ˜¯å¦å…¨é€‰
        this.checkAll = checkedCount == this.allCheckData.length;
        // åˆ¤æ–­æ˜¯å¦åŠé€‰
        this.isIndeterminate = checkedCount > 0 && checkedCount < this.allCheckData.length;
      }
    }
  };
</script>

<style scoped lang="scss">
  .set-name{
    display: inline-block;
    width: 106px;
  }

  .el-checkbox-group{
    margin-left: 110px;
  }

  .check-all{
    margin-bottom: 10px;
  }

  .el-checkbox{
    margin: 0 30px 0 0;
  }
</style>



