<template>
  <!--èœå•åˆ—è¡¨-->
  <div class="role-manage">
    <el-button
            type="primary"
            @click.native="showDialog('create')"
            icon="el-icon-plus">
      æ–°å»ºèœå•
    </el-button>
    <el-table
            :data="tableData"
            style="width: 100%">
      <el-table-column
              prop="menu_id"
              label="NO.">
      </el-table-column>
      <el-table-column
              prop="menu_name"
              label="èœå•åç§°">
      </el-table-column>
      <el-table-column
              prop="url"
              label="èœå•ç¼–ç ">
      </el-table-column>
      <el-table-column
              prop="content"
              label="æè¿°">
      </el-table-column>
      <el-table-column
              label="æ“ä½œ">
        <template slot-scope="scope">
          <el-button
                  @click.native.prevent="showDialog('edit', scope.row)"
                  type="text"
                  size="small">
            ç¼–è¾‘
          </el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
            @size-change="handleSizeChange"
            @current-change="changePage"
            :current-page.sync="page"
            :page-sizes="[20, 50, 100, 200]"
            :page-size="pageSize"
            layout="total, sizes, prev, pager, next"
            prev-text="ä¸Šä¸€é¡µ"
            next-text="ä¸‹ä¸€é¡µ"
            :total="totalCount">
    </el-pagination>
    <el-dialog
            :title="dialogTitle"
            :visible.sync="show"
            width="30%">
      <el-form
              ref="menu"
              :model="formParams"
              :rules="rules"
              label-width="100px">
        <el-form-item
                prop="menuName"
                class="is-required"
                label="èœå•åç§°">
          <el-input v-model="formParams.menuName"></el-input>
        </el-form-item>
        <el-form-item
                class="is-required"
                prop="menuCode"
                label="èœå•ç¼–ç ">
          <el-input v-model="formParams.menuCode"></el-input>
        </el-form-item>
        <el-form-item label="æè¿°">
          <el-input v-model="formParams.menuContent"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="show = false">å– æ¶ˆ</el-button>
        <el-button
                v-show="dialogTitle === 'æ–°å»ºèœå•'"
                type="primary"
                @click="save('create')">
          ç¡® å®š
        </el-button>
        <el-button
                v-show="dialogTitle === 'ç¼–è¾‘èœå•'"
                type="primary"
                @click="save('edit')">
          ç¡® å®š
        </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
  import { getMenuList, createMenu, updateMenu } from '../../api/api'
  export default {
    data () {
      return {
        // tableæ‰€æœ‰æ•°æ®
        tableData: [],
        // å½“å‰ç¿»é¡µæ•°
        page: 1,
        // æ€»é¡µæ•°
        totalPage: 3,
        // tableæ€»æ¡æ•°
        totalCount: 1,
        // æ¯é¡µæ˜¾ç¤ºçš„æ¡æ•°
        pageSize: 20,
        dialogTitle: '',
        show: false,
        formParams: {
          menuId: '',
          // èœå•åç§°
          menuName: '',
          // èœå•ç¼–ç 
          menuCode: '',
          // æè¿°
          menuContent: ''
        },
        rules: {
          menuName: [{
            required: true, message: 'èœå•åç§°ä¸èƒ½ä¸ºç©º'
          }],
          menuCode: [{
            required: true, message: 'èœå•ç¼–ç ä¸èƒ½ä¸ºç©º'
          }]
        }
      }
    },
    mounted () {
      this.getMenuList(1, 20)
    },
    methods: {
      /**
       * èŽ·å–èœå•åˆ—è¡¨
       * @param {Number} page é¡µç 
       * @param {Number} pageSize æ¯é¡µæ˜¾ç¤ºå¤šå°‘æ¡
       */
      getMenuList (page, pageSize) {
        let params = {
          page_size: pageSize,
          page_num: page
        }
        this.page = page
        this.pageSize = pageSize
        getMenuList(params).then(res => {
          if (res.data.error_no === 0) {
            this.tableData = res.data.result.menus
            this.totalCount = parseInt(res.data.result.total_count)
          } else {
            this.$message.error(res.data.error_msg)
          }
        })
          .catch(error => {
            this.$message.error('æœåŠ¡å™¨å‚²å¨‡äº†ï¼Œè¯·åˆ·æ–°é‡è¯•')
          })
      },
      changePage (val) {
        this.getMenuList(val, this.pageSize)
      },
      handleSizeChange (val) {
        this.getMenuList(1, val)
      },
      /**
       * æ–°å»ºã€ç¼–è¾‘èœå•
       * @param {String} operation æ“ä½œï¼ˆç¼–è¾‘ or åˆ é™¤ï¼‰
       * @param {Object} data
       */
      showDialog (operation, data = {}) {
        this.show = true
        if (operation ===  'create') {
          this.$nextTick(() => {
            this.$refs['menu'].resetFields()
            this.formParams.menuName = ''
            this.formParams.menuCode = ''
            this.formParams.menuContent = ''
            this.formParams.menuId = ''
          })
          this.dialogTitle = 'æ–°å»ºèœå•'
        } else {
          this.formParams.menuName = data.menu_name || ''
          this.formParams.menuCode = data.url || ''
          this.formParams.menuContent = data.content || ''
          this.formParams.menuId = data.menu_id || ''
          this.dialogTitle = 'ç¼–è¾‘èœå•'
        }
      },
      /**
       * ä¿å­˜
       * @param {String} operation æ“ä½œï¼ˆç¼–è¾‘ or åˆ é™¤ï¼‰
       */
      save (operation) {
        this.formParams.menuName = this.formParams.menuName.trim()
        this.formParams.menuCode = this.formParams.menuCode.trim()
        this.$refs['menu'].validate((valid) => {
          if (valid) {
            let params = {
              menu_name: this.formParams.menuName,
              content: this.formParams.menuContent,
              url: this.formParams.menuCode
            }
            if (operation === 'create') {
              createMenu(params).then(res => {
                if (res.data.error_no === 0) {
                  this.$message.success('ä¿å­˜æˆåŠŸ')
                  this.getMenuList(1, this.pageSize)
                  this.show = false
                } else {
                  this.$message.error(res.data.error_msg)
                }
              })
                .catch(error => {
                  this.$message.error('æœåŠ¡å™¨å‚²å¨‡äº†ï¼Œè¯·åˆ·æ–°é‡è¯•')
                })
            } else {
              params.menu_id = this.formParams.menuId
              updateMenu(params).then(res => {
                if (res.data.error_no === 0) {
                  this.$message.success('ä¿å­˜æˆåŠŸ')
                  this.show = false
                  this.getMenuList(this.page, this.pageSize)
                } else {
                  this.$message.error(res.data.error_msg)
                }
              })
                .catch((error) => {
                  this.$message.error('æœåŠ¡å™¨å‚²å¨‡äº†ï¼Œè¯·åˆ·æ–°é‡è¯•')
                })
            }
          }
        })
      }
    }
  }
</script>



