<template>
  <div class="role-manage">
    <el-button
      type="primary"
      @click="createUser"
      icon="el-icon-plus">
      æ–°å¢žè§’è‰²
    </el-button>
    <el-table
      :data="tableData"
      style="width: 100%">
      <el-table-column
        prop="role_id"
        label="NO.">
      </el-table-column>
      <el-table-column
        prop="role_name"
        label="è§’è‰²åç§°">
      </el-table-column>
      <el-table-column
        prop="creator"
        label="åˆ›å»ºäºº">
      </el-table-column>
      <el-table-column
        label="åˆ›å»ºæ—¶é—´">
        <template slot-scope="scope">
          {{formatDate(scope.row.create_time)}}
        </template>
      </el-table-column>
      <el-table-column
        label="çŠ¶æ€">
        <template slot-scope="scope">
          {{scope.row.enabled == '1' ? 'å·²å¯ç”¨' : 'å·²ç¦ç”¨'}}
        </template>
      </el-table-column>
      <el-table-column
        label="æ“ä½œ">
        <template slot-scope="scope">
          <el-button
            @click.native.prevent="linkToEdit(scope.row.role_id)"
            type="text"
            size="small">
            å…³è”æƒé™
          </el-button>
          <el-button
            @click.native.prevent="changeStatus(scope.row.role_id, scope.row.enabled)"
            type="text"
            size="small">
            {{scope.row.enabled == '1' ? 'ç¦ç”¨' : 'å¯ç”¨'}}
          </el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      @current-change="changePage"
      :current-page.sync="page"
      :page-size="20"
      layout="total, prev, pager, next"
      :total="totalCount"
      prev-text="ä¸Šä¸€é¡µ"
      next-text="ä¸‹ä¸€é¡µ">
    </el-pagination>
  </div>
</template>

<script>
import { getRoleList, updateRole } from '../api/api'
// æ—¶é—´æˆ³è½¬å­—ç¬¦ä¸²
const timeStampToDate = (timeStamp) => {
  timeStamp = parseInt(timeStamp)
  let date = new Date(timeStamp)
  let result = ''
  result += date.getFullYear() + '-'
  result += ((date.getMonth() + 1) > 9 ? date.getMonth() + 1 : '0' + (date.getMonth() + 1)) + '-'
  result += (date.getDate() > 9 ? date.getDate() : '0' + date.getDate()) + ' '
  result += (date.getHours() > 9 ? date.getHours() : '0' + date.getHours()) + ':'
  result += (date.getMinutes() > 9 ? date.getMinutes() : '0' + date.getMinutes()) + ':'
  result += date.getSeconds() > 9 ? date.getSeconds() : '0' + date.getSeconds()
  return result
}

export default {
  data () {
    return {
      // tableæ‰€æœ‰æ•°æ®
      tableData: [],
      // å½“å‰ç¿»é¡µæ•°
      page: 1,
      // æ€»é¡µæ•°
      totalPage: 3,
      // tableæ€»æ¡æ•°
      totalCount: 1
    }
  },
  methods: {
    // ä¿®æ”¹è§’è‰²çŠ¶æ€
    changeStatus (id, status) {
      // åˆ¤æ–­å½“å‰æ˜¯å¯ç”¨è¿˜æ˜¯ç¦ç”¨
      const conformText = status == '1' ? 'ç¦ç”¨' : 'å¯ç”¨'
      const enableStatus = status == '1' ? 2 : 1
      // å¼¹çª—æç¤º
      this.$confirm('ç¡®å®š' + conformText + 'è§’è‰²?', 'æç¤º', {
        confirmButtonText: 'ç¡®å®š',
        cancelButtonText: 'å–æ¶ˆ',
        type: 'warning'
      }).then(() => {
        // ä¿®æ”¹ç”¨æˆ·çŠ¶æ€
        const postData = {
          role_id: id,
          enable: enableStatus
        }
        updateRole(postData).then(response => {
          const res = response.data
          if (res.error_no === 0) {
            this.$message.success(conformText + 'æˆåŠŸ!')
            this.getTableData(this.page)
          } else {
            this.$message.error(res.error_msg)
          }
        })
        .catch(error => {
          this.$message.error('æœåŠ¡å™¨å‚²å¨‡äº†ï¼Œè¯·åˆ·æ–°é‡è¯•')
        })
      })
    },
    // è¡¨æ ¼ä¸­å±•ç¤ºçš„çŠ¶æ€æ–‡æ¡ˆ
    formatDate (timeStamp) {
      return timeStampToDate(timeStamp * 1000)
    },
    // èŽ·å–tableæ•°æ®
    getTableData (page) {
      const getParams = {
        page_num: page
      }
      getRoleList(getParams).then(response => {
        let res = response.data
        if (res.error_no === 0) {
          this.tableData = res.result.roles
          this.totalCount = parseInt(res.result.total_count, 10)
          this.totalPage = Math.ceil(this.totalCount / 20)
        } else {
          this.$message.error(res.error_msg)
        }
      })
      .catch(error => {
        this.$message.error('æœåŠ¡å™¨å‚²å¨‡äº†ï¼Œè¯·åˆ·æ–°é‡è¯•')
      })
    },
    // ç¿»é¡µäº‹ä»¶
    changePage (val) {
      this.getTableData(val)
    },
    createUser () {
      this.$router.push({path: '/roleCreate'})
    },
    linkToEdit (id) {
      this.$router.push({path: '/roleEdit', query: {role_id: id}})
    }
  },
  mounted () {
    // èŽ·å–åˆå§‹æ•°æ®
    this.getTableData(this.page)
  }
}
</script>



