<template>
  <div class="user">
      <el-row class="search">
        <el-col :span="24">
            <el-form :inline="true" :model="searchForm" ref="searchForm" label-width="100px">
                <el-form-item label="ç”¨æˆ·å" prop="user">
                    <el-input v-model="searchForm.user" placeholder="è¯·è¾“å…¥ç”¨æˆ·å"></el-input>
                </el-form-item>
                <el-form-item label="å§“å" prop="name">
                    <el-input v-model="searchForm.name" placeholder="è¯·è¾“å…¥å§“å"></el-input>
                </el-form-item>
                <el-form-item label="æ‰‹æœºå·ç " prop="phoneNumber">
                    <el-input v-model="searchForm.phoneNumber" placeholder="è¯·è¾“å…¥æ‰‹æœºå·ç "></el-input>
                </el-form-item>
                <el-form-item label="çŠ¶æ€" prop="status">
                    <el-select v-model="searchForm.status" placeholder="çŠ¶æ€">
                        <el-option label="å…¨éƒ¨" value="all"></el-option>
                        <el-option label="å·²å¯ç”¨" value="1"></el-option>
                        <el-option label="å·²ç¦ç”¨" value="2"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="å¸å·ç±»åž‹" prop="userType">
                    <el-select v-model="searchForm.userType" placeholder="å¸å·ç±»åž‹">
                        <el-option label="å…¨éƒ¨" value="all"></el-option>
                        <el-option label="sso" value="sso"></el-option>
                        <el-option label="wmpass" value="wmpass"></el-option>
                    </el-select>
                </el-form-item>
            </el-form>
            <div class="search-btns">
                <el-button type="primary" @click="search">æŸ¥è¯¢</el-button>
                <el-button @click="reset">é‡ç½®</el-button>
            </div>
        </el-col>
      </el-row>
      <div class="user-manage">
          <el-button
                  type="primary"
                  @click="createUser"
                  icon="el-icon-plus">
              ç»‘å®šç”¨æˆ·
          </el-button>
          <el-table
                  :data="tableData"
                  style="width: 100%">
              <el-table-column
                      prop="user_id"
                      width="80"
                      label="NO.">
              </el-table-column>
              <el-table-column
                      prop="user_name"
                      width="90"
                      label="ç”¨æˆ·å">
              </el-table-column>
              <el-table-column
                      prop="type"
                      width="90"
                      label="å¸å·ç±»åž‹">
              </el-table-column>
              <el-table-column
                      width="80"
                      prop="real_name"
                      label="å§“å">
              </el-table-column>
              <el-table-column
                      prop="phone"
                      width="120"
                      label="æ‰‹æœºå·">
              </el-table-column>
              <el-table-column
                      prop="email"
                      label="é‚®ç®±">
              </el-table-column>
              <el-table-column
                      width="80"
                      label="çŠ¶æ€">
                  <template slot-scope="scope">
                      {{scope.row.enabled == '1' ? 'å·²å¯ç”¨' : 'å·²ç¦ç”¨'}}
                  </template>
              </el-table-column>
              <el-table-column
                  prop="time"
                  label="åˆ›å»ºæ—¶é—´">
              </el-table-column>
              <el-table-column
                      label="æ“ä½œ">
                  <template slot-scope="scope">
                      <el-button
                              @click.native.prevent="linkToEdit(scope.row.user_id)"
                              type="text"
                              size="small">
                          å…³è”è§’è‰²
                      </el-button>
                      <el-button
                              v-if="isWms"
                              @click.native.prevent="editPermission(scope.row.user_id)"
                              type="text"
                              size="small">
                          æ•°æ®æƒé™
                      </el-button>
                      <el-button
                              @click.native.prevent="changeStatus(scope.row.user_id, scope.row.enabled)"
                              type="text"
                              size="small">
                          {{scope.row.enabled == '1' ? 'ç¦ç”¨' : 'å¯ç”¨'}}
                      </el-button>
                  </template>
              </el-table-column>
          </el-table>
          <el-pagination
                  @current-change="changePage"
                  :current-page.sync="page"
                  :page-size="20"
                  layout="total, prev, pager, next"
                  :total="totalCount"
                  prev-text="ä¸Šä¸€é¡µ"
                  next-text="ä¸‹ä¸€é¡µ">
          </el-pagination>
      </div>
  </div>
</template>

<script>
import { getUserList, updateUser } from '../api/api'

export default {
  data () {
    return {
      // åˆ—è¡¨é¡µtableæ‰€æœ‰æ•°æ®
      tableData: [],
      // å½“å‰ç¿»é¡µæ•°
      page: 1,
      // æ€»é¡µæ•°
      totalPage: 1,
      // tableæ€»æ¡æ•°
      totalCount: 1,
      // æœç´¢è¡¨å•çš„æ•°æ®
      searchForm: {
        user: '',
        name: '',
        phoneNumber: '',
        status: 'all',
        userType: 'all'
      },
      // å½©äº‘ç³»ç»Ÿè¿˜æ˜¯æ²§æµ·ç³»ç»Ÿ
      isWms: window.location.href.indexOf('wms') >= 0
    }
  },
  methods: {
    // ä¿®æ”¹ç”¨æˆ·çŠ¶æ€
    changeStatus (id, status) {
      // åˆ¤æ–­å½“å‰æ˜¯å¯ç”¨è¿˜æ˜¯ç¦ç”¨
      const conformText = status == '1' ? 'ç¦ç”¨' : 'å¯ç”¨'
      const enableStatus = status == '1' ? 2 : 1
      // å¼¹çª—æç¤º
      this.$confirm('ç¡®å®š' + conformText + 'ç”¨æˆ·?', 'æç¤º', {
        confirmButtonText: 'ç¡®å®š',
        cancelButtonText: 'å–æ¶ˆ',
        type: 'warning'
      }).then(() => {
        // ä¿®æ”¹ç”¨æˆ·çŠ¶æ€
        const postData = {
          user_id: id,
          enable: enableStatus
        }
        updateUser(postData).then(response => {
          const res = response.data
          if (res.error_no === 0) {
            this.$message.success(conformText + 'æˆåŠŸ!')
            this.resetParams()
            this.getTableData(this.page)
          } else {
            this.$message.error(res.error_msg)
          }
        })
        .catch(error => {
          this.$message.error('æœåŠ¡å™¨å‚²å¨‡äº†ï¼Œè¯·åˆ·æ–°é‡è¯•')
        })
      })
    },
    // èŽ·å–tableæ•°æ®
    getTableData (page) {
      const getParams = {
        page_num: page,
        user_name: this.searchForm.user, // ç”¨æˆ·å
        real_name: this.searchForm.name, // å§“å
        phone: this.searchForm.phoneNumber, // æ‰‹æœºå·ç 
        enable: this.searchForm.status === 'all' ? '' : parseInt(this.searchForm.status), // æ˜¯å¦ç¦ç”¨
        type:  this.searchForm.userType === 'all' ? '' : this.searchForm.userType// ç”¨æˆ·ç±»åž‹
      }
      getUserList(getParams).then(response => {
        const res = response.data
        if (res.error_no === 0) {
          res.result.users = res.result.users.map(item => {
            item.time = this.handleTimestamp(item.create_time)
            return item
          })
          this.tableData = res.result.users
          this.totalCount = parseInt(res.result.total_count, 10)
          this.totalPage = Math.ceil(this.totalCount / 20)
        } else {
          this.$message.error(res.error_msg)
        }
      })
      .catch(error => {
        this.$message.error('æœåŠ¡å™¨å‚²å¨‡äº†ï¼Œè¯·åˆ·æ–°é‡è¯•')
      })
    },
    handleTimestamp (timestamp) {
      timestamp = parseInt(timestamp) * 1000
      let date = new Date(timestamp)
      let result = ''
      result += date.getFullYear() + '-'
      result += ((date.getMonth() + 1) > 9 ? date.getMonth() + 1 : '0' + (date.getMonth() + 1)) + '-'
      result += (date.getDate() > 9 ? date.getDate() : '0' + date.getDate()) + ' '
      result += (date.getHours() > 9 ? date.getHours() : '0' + date.getHours()) + ':'
      result += (date.getMinutes() > 9 ? date.getMinutes() : '0' + date.getMinutes()) + ':'
      result += date.getSeconds() > 9 ? date.getSeconds() : '0' + date.getSeconds()
      return result
    },
    // ç¿»é¡µäº‹ä»¶
    changePage (val) {
      this.getTableData(val)
    },
    // è·³è½¬æ–°å¢žç”¨æˆ·
    createUser () {
      this.$router.push({ path: '/userCreate'})
    },
    linkToEdit (id) {
      this.$router.push({path: '/userEdit', query: {user_id: id}})
    },
    // ç¼–è¾‘æ•°æ®æƒé™
    editPermission (id) {
      this.$router.push({
        path: '/dataPermission',
        query: {user_id: id}})
    },
    // æœç´¢
    search () {
      this.page = 1
      this.getTableData(this.page)
    },
    resetParams () {
      this.$refs['searchForm'].resetFields()
    },
    // é‡ç½®
    reset () {
      this.resetParams()
      this.getTableData(1)
    }
  },
  mounted () {
    // èŽ·å–åˆå§‹æ•°æ®
    this.getTableData(this.page)
  }
}
</script>

<style>
    .search {
        margin-bottom: 20px;
    }
    .search-btns {
        margin-left: 100px;
    }
    .list-wrap .user-management {
        background: none;
        padding: 0;
    }
    .search, .user-manage {
        background: #fff;
        padding: 20px;
    }
</style>


