import axios from 'axios'
import qs from 'qs'
// urlé‡Œå–å‚æ•°
const getQueryByNameFromUrl = (name, url) => {
  let reg = new RegExp('(^|&)' + name + '=([^&]*)(&|$)', 'i')
  let r = url.split('?')[1].match(reg)
  let context = ''
  if (r != null) { context = r[2] }
  reg = null
  r = null
  return context == null || context === '' || context === 'undefined' ? '' : context
}

// ç»Ÿä¸€å¯¹ç™»å½•æƒ…å†µåšå¤„ç†
axios.interceptors.response.use(function (response) {
  const res = response.data
  if (res.error_no === 1102) {
    let redirectUrl
    if (res.result.platform === 1) {
      redirectUrl = res.result.login_url + '?from=' + encodeURIComponent(window.location.href)
    } else {
      // åˆ¤æ–­æœªç™»å½•çš„æƒ…å†µä¸‹ï¼Œè·³è½¬åˆ°ç™»å½•é¡µï¼Œå¹¶æŠŠredirect_urlå‚æ•°åšæ›¿æ¢
      redirectUrl = res.result.login_url.replace(getQueryByNameFromUrl('redirect_url', res.result.login_url), encodeURIComponent(window.location.href))
    }
    window.location.href = redirectUrl
  } else {
    return response
  }
}, function (error) {
  // å½“å“åº”å¼‚å¸¸æ—¶åšä¸€äº›å¤„ç†
  return Promise.reject(error)
})
