<template>
  <div class='export-container'>
    <span class='explain-text'>è¯´æ˜Žï¼šå¯¼å‡ºä»»åŠ¡æ–‡ä»¶æœ€å¤šä¿ç•™30å¤©ï¼Œè¿‡æœŸè¯·é‡æ–°å¯¼å‡º</span>
    <el-table :data="exportList" style='width: 100%' :fit='true'>
      <el-table-column prop="id" label="ä»»åŠ¡ID"></el-table-column>
      <el-table-column prop="task_name" label="ä»»åŠ¡åç§°"></el-table-column>
      <el-table-column label="ä»»åŠ¡åˆ›å»ºæ—¶é—´">
        <template slot-scope='scope'>
          <span>{{ stampToDate(scope.row.create_time) }}</span>
        </template>
      </el-table-column>
      <el-table-column label="ä»»åŠ¡å®Œæˆæ—¶é—´">
        <template slot-scope='scope'>
          <span v-if='scope.row.status === 1 || scope.row.status === 5'>{{ stampToDate(scope.row.update_time) }}</span>
        </template>
      </el-table-column>
      <el-table-column label="å½“å‰çŠ¶æ€">
        <template slot-scope='scope'>
          <span>{{ scope.row.status === 1 ? 'å·²å®Œæˆ' : scope.row.status === 2 ? 'æŽ’é˜Ÿä¸­' : scope.row.status === 3 ? 'ä»»åŠ¡å¤±è´¥' : scope.row.status === 4 ? 'æ­£åœ¨ç”Ÿæˆæ–‡ä»¶' : 'æ–‡ä»¶è¿‡æœŸ' }}</span>
          <span v-if='scope.row.status === 2'>(å·²æœ‰{{ scope.row.numberOfQueues }}ä¸ªä»»åŠ¡æŽ’é˜Ÿä¸­)</span>
        </template>
      </el-table-column>
      <el-table-column prop="user_name" label="æ“ä½œäºº"></el-table-column>
      <el-table-column label="æ“ä½œ">
        <template slot-scope='scope'>
          <a class='el-button--text' :href="scope.row.url" v-if='scope.row.status === 1 || scope.row.status === 5' :class='{unDownLoad: scope.row.status === 5}'>ä¸‹è½½</a>
          <span v-if='scope.row.status === 3'>å¤±è´¥åŽŸå› ï¼š{{ scope.row.err_msg }}</span>
        </template>
      </el-table-column>
    </el-table>
    <div class='page-box'>
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="currentPage"
        :page-sizes="[10, 20, 50, 100]"
        :page-size="pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </div>
  </div>
</template>

<style lang='scss' scoped>
  .export-container {
    padding: 20px 30px;
    background-color: #fff;
    .explain-text {
      color: #999;
    }
    .page-box {
      margin-top: 20px;
      height: 32px;
      .el-pagination {
        float: right;
      }
    }
    a {
      text-decoration: none;
    }
    .unDownLoad {
      color: #909399;
      pointer-events: none;
    }
  }
</style>

<script>
  import axios from 'axios'
  import qs from 'qs'
  const getReportTaskList = params => {
    return axios({
      method: 'post',
      url: '/authui/report/getreporttasklist',
      data: qs.stringify(params)
    })
  }

  export default {
    data () {
      return {
        app_id: this.$route.query.app_id,
        exportList: [],
        currentPage: 1,
        pageSize: 20,
        total: 0
      }
    },
    created () {
      this.getExportList()
    },
    methods: {
      getExportList () {
        let params = {}
        params.app_id = this.app_id
        params.page = this.currentPage
        params.limit = this.pageSize
        getReportTaskList(params).then((res) => {
          if (res && res.data && res.data.error_no === 0) {
            let result = res.data.result || {}
            this.total = result.total || 0
            this.exportList = result.detail || []
          } else {
            this.$message.error(res.data.error_msg || 'è¯·æ±‚æ•°æ®å‡ºé”™ï¼Œè¯·ç¨å€™é‡è¯•')
          }
        })
      },
      stampToDate (time) {
        let date = new Date(time * 1000)
        let y = date.getFullYear()
        let m = date.getMonth() + 1
        m = m < 10 ? ('0' + m) : m
        let d = date.getDate()
        d = d < 10 ? ('0' + d) : d
        let h = date.getHours()
        let min = date.getMinutes()
        let s = date.getSeconds()
        min = min < 10 ? ('0' + min) : min
        s = s < 10 ? ('0' + s) : s
        return y + '-' + m + '-' + d + ' ' + h + ':' + min + ':' + s
      },
      handleSizeChange (val) {
        this.pageSize = val
        this.getExportList()
      },
      handleCurrentChange (val) {
        this.currentPage = val
        this.getExportList()
      }
    }
  }
</script>





