      <el-menu-item index="2"><router-link :to="{ path: '/createSupplyAccount',query:{create: vendorId, name: this.$route.query.name}}" v-if="!!vendorId">ä¾›è´§å•†åŽå°å¸å·</router-link></el-menu-item>
      <el-menu-item index="3"><router-link :to="{ path: '/createPond',query:{create: vendorId, name: this.$route.query.name}}" v-if="!!vendorId">å•†å“æ± </router-link></el-menu-item>
    </el-menu>

  <el-form ref="tab" label-width="119px" :rules="rules" :label-position="labelPosition" :model="tab" class="demo-ruleForm">
<span class="supplyName">{{this.$route.query.name}}</span>

  <el-form-item label="ä¾›è´§å•†ç™»å½•å"  class="tab2" prop="name">
    <el-input v-model="tab.name" class="i380" placeholder="6ï½ž20ä½å­—æ¯æˆ–å­—æ¯æ•°å­—ç»„åˆ" :disabled="writable"></el-input>
  </el-form-item>

  <el-form-item label="ç»‘å®šæ‰‹æœºå·"  class="tab2" prop="tel">
    <el-input v-model="tab.tel" class="i380" placeholder="è¯·è¾“å…¥ç»‘å®šæ‰‹æœºå·" :disabled="writable"></el-input>
  </el-form-item>

  <el-form-item>
    <el-button type="primary" @click="submitForm('tab')" :disabled="btn.updatevendoradmin">ä¿å­˜</el-button>
  </el-form-item>

  </el-form>
  
  <el-dialog
    title="æç¤º"
    :visible.sync="dialogVisible"
    width="30%">
    <span>åˆ›å»ºæˆåŠŸï¼Œå·²å°†ç™»å½•ç½‘å€ã€ç™»å½•åã€åˆå§‹å¯†ç å‘é€çŸ­ä¿¡åˆ°ä¾›è´§å•†çš„è¯¥ç»‘å®šæ‰‹æœºå·</span>
    <span slot="footer" class="dialog-footer">
      <el-button type="primary" @click="dialogVisible = false">ç¡® å®š</el-button>
    </span>
  </el-dialog>

  </section>

</template>
<script>
import qs from 'qs'
import { updateVendorAdmin, getCityWarehouse } from '../../api/api'
import util from '../../common/js/util'
export default {
  data () {
    return {
      btn: {
        updatevendoradmin: !util.jdugeShow('vendor-main-updatevendoradmin')
      },
      activeName: '1',
      activeIndex: '2',
      vendor_id: '',
      labelPosition: 'left',
      dialogVisible: false,
      writable: false,
      vendorId: this.$route.query.create,
      tab: {
        name: '',
        tel: '',
        supplyName: this.$route.query.name
      },
      rules: {
        name: [
          { required: true, validator: 'regexp', pattern: /^[a-zA-Z0-9]{6,20}$/, message: 'è¯·è¾“å…¥6ï½ž20ä½å­—æ¯æˆ–å­—æ¯æ•°å­—ç»„åˆ', trigger: 'change,blur' }
        ],
        tel: [
          { required: true, validator: 'regexp', pattern: /^1\d{10}$/, message: 'è¯·è¾“å…¥æ­£ç¡®çš„æ‰‹æœºå·', trigger: 'change,blur' }
        ]
      }
    }
  },
  created () {
    this.getVendorById()
  },
  methods: {
    getVendorById () {
      let params = {
        vendor_id: this.vendorId
      }
      getCityWarehouse(params).then((res) => {
        let result = res.data.result
        if (result.vendor_login_account_id) {
          this.tab.name = result.vendor_login_account_name
          this.tab.tel = result.vendor_login_account_mobile
          this.writable = true
          this.btn.updatevendoradmin = true
        }
      })
    },
    handleClick (tab, event) {
        // console.log(tab, event)ï¼›
    },
    updateVendorAdmin () {
      let params = {
        vendor_id: this.vendorId,
        vendor_login_account: this.tab.name,
        vendor_login_mobile: this.tab.tel
      }

      updateVendorAdmin(qs.stringify(params)).then((res) => {
        if (res.data.error_no === 0) {
          this.dialogVisible = true
          this.writable = true
        } else {
          this.$message({message: res.data.error_msg, type: 'error'})
        }
      })
    },

    submitForm (formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.updateVendorAdmin()
        } else {
          this.$message({message: 'è¯·å¡«å†™æ­£ç¡®ä¿¡æ¯', type: 'error'})
          return false
        }
      })
    }
  }
}
</script>

<style scoped lang="scss">
section{
