<template>
  <div class="role-manage">
    <el-button
            type="primary"
            @click="createPermission"
            icon="el-icon-plus">
      æ–°å»ºæƒé™
    </el-button>
    <el-table
            :data="tableData"
            style="width: 100%">
      <el-table-column
              prop="permission_id"
              label="NO.">
      </el-table-column>
      <el-table-column
              prop="permission_name"
              label="æƒé™åç§°">
      </el-table-column>
      <el-table-column
              prop="set_name"
              label="æƒé™é›†åˆ">
      </el-table-column>
      <el-table-column
              label="æ“ä½œ">
        <template slot-scope="scope">
          <el-button
                  @click.native.prevent="editPermission(scope.row.permission_id)"
                  type="text"
                  size="small">
            ç¼–è¾‘
          </el-button>
          <el-button
                  @click.native.prevent="changeStatus(scope.row)"
                  type="text"
                  size="small">
            {{scope.row.enabled == '1' ? 'ç¦ç”¨' : 'å¯ç”¨'}}
          </el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
            @size-change="handleSizeChange"
            @current-change="changePage"
            :current-page.sync="page"
            :page-sizes="[20, 50, 100, 200]"
            :page-size="pageSize"
            layout="total, sizes, prev, pager, next"
            prev-text="ä¸Šä¸€é¡µ"
            next-text="ä¸‹ä¸€é¡µ"
            :total="totalCount">
    </el-pagination>
  </div>
</template>

<script>
  import { getPermissionList, updatePermission } from '../../api/api'

  export default {
    data () {
      return {
        // tableæ‰€æœ‰æ•°æ®
        tableData: [],
        // å½“å‰ç¿»é¡µæ•°
        page: 1,
        // æ€»é¡µæ•°
        totalPage: 3,
        // tableæ€»æ¡æ•°
        totalCount: 1,
        // æ¯é¡µæ˜¾ç¤ºæ¡æ•°
        pageSize: 20
      }
    },
    mounted () {
      this.getPermissionList(this.page, this.pageSize)
    },
    methods: {
      /**
       * æ–°å»ºæƒé™
       */
      createPermission () {
        this.$router.push({path: '/createPermission'})
      },
      /**
       * èŽ·å–æƒé™åˆ—è¡¨
       * @param {Number} page é¡µç 
       * @param {Number} pageSize æ¯é¡µæ˜¾ç¤ºæ¡æ•°
       */
      getPermissionList (page, pageSize) {
        this.page = page
        this.pageSize = pageSize
        let params = {
          permission_name: '',
          page_size: pageSize,
          page_num: page
        }
        getPermissionList(params).then(res => {
          if (res.data.error_no === 0) {
            this.tableData = res.data.result.permissions
            this.totalCount = parseInt(res.data.result.total_count)
          } else {
            this.$message.error(res.data.error_msg)
          }
        })
        .catch(error => {
          this.$message.error('æœåŠ¡å™¨å‚²å¨‡äº†ï¼Œè¯·åˆ·æ–°é‡è¯•')
        })
      },
      changePage (val) {
        this.getPermissionList(val, this.pageSize)
      },
      handleSizeChange (val) {
        this.getPermissionList(1, val)
      },
      /**
       * ç¼–è¾‘æƒé™
       * @param {String} id æƒé™id
       */
      editPermission (id) {
        this.$router.push({path: '/editPermission', query: {permission_id: id}})
      },
      /**
       * ç¦ç”¨å¯ç”¨çŠ¶æ€åˆ‡æ¢
       * @param {Object} data
       */
      changeStatus (data) {
        const conformText = data.enabled == '1' ? 'ç¦ç”¨' : 'å¯ç”¨'
        const enableStatus = data.enabled == '1' ? 2 : 1
        // å¼¹çª—æç¤º
        this.$confirm('ç¡®å®š' + conformText + 'æƒé™?', 'æç¤º', {
          confirmButtonText: 'ç¡®å®š',
          cancelButtonText: 'å–æ¶ˆ',
          type: 'warning'
        }).then(() => {
          // ä¿®æ”¹æƒé™çŠ¶æ€
          const postData = {
            permission_id: data.permission_id,
            enable: enableStatus
          }
          updatePermission(postData).then(response => {
            const res = response.data
            if (res.error_no === 0) {
              this.$message.success(conformText + 'æˆåŠŸ!')
              this.getPermissionList(this.page, this.pageSize)
            } else {
              this.$message.error(res.error_msg)
            }
          })
            .catch(error => {
              this.$message.error('æœåŠ¡å™¨å‚²å¨‡äº†ï¼Œè¯·åˆ·æ–°é‡è¯•')
            })
        })
          .catch(() => {
          })
      }
    }
  }
</script>



