      let categoryOneText = document.getElementById('select1').value
      let categoryTwoText = document.getElementById('select2').value
      let categoryThrText = document.getElementById('select3').value
      let skuCategoryText = `${categoryOneText},${categoryTwoText},${categoryThrText}`
      let params = {
        sku_id: this.edit_sku_id || '',
        sku_category_text: skuCategoryText,
        sku_name: this.form.name,
        sku_category_1: this.form.value1,
        sku_category_2: this.form.value2,
        sku_category_3: this.form.value3,
        sku_tax_rate: this.form.tax_rate,
        upcs: JSON.stringify(this.form.standard),
        sku_safe_stock_num: Number(this.form.skuSafeStockNum),
        sku_from_country: Number(this.form.skuFromCountry),
        sku_effect_type: Number(this.form.skuEffectType),
        sku_effect_alarm_day: Number(this.form.skuEffectAlarmDay),
        sku_net: this.form.skuNet,
        sku_net_unit: Number(this.form.skuNetUnit),
        sku_business_form: JSON.stringify(this.form.skuBusinessForm)
      }
      if (params.sku_net_unit !== 1 && params.sku_net_unit !== 2) {
        params.sku_weight = this.form.skuWeight
      }
      if (params.sku_effect_type === 1) {
        params.sku_effect_day = this.form.skuEffectDay
      }
      if (this.edit_sku_id) {
        params.sku_image = JSON.stringify(this.form.fileArr)
        updateSku(qs.stringify(params)).then((res) => {
          if (res.data.error_no === 0) {
            this.$message({message: 'ä¿å­˜æˆåŠŸ', type: 'success'})
            this.$router.push({path: `/updateSupplyInfo?sku_id=${this.edit_sku_id}`})
          } else {
            this.$message({message: res.data.error_msg, type: 'error'})
          }
        })
      } else {
        params.sku_image = JSON.stringify(this.form.fileArr)
        createSku(qs.stringify(params)).then((res) => {
          if (res.data.error_no === 0) {
            this.$message({message: 'ä¿å­˜æˆåŠŸ', type: 'success'})
            this.$router.push({path: `/updateSupplyInfo?sku_id=${res.data.result.sku_id}`})
          } else {
            this.$message({message: res.data.error_msg, type: 'error'})
          }
        })
      }
    },
    handleExact () {
      this.$message({
        message: 'æœ€å¤šåªèƒ½ä¼ 6å¼ ',
        type: 'warning'
      })
    },
    handleSuccess (response, file, fileList) {
      let addPics = this.mangePics(fileList)
      this.form.fileArr = addPics
      this.form.fileUrl = addPics[0].url
    },
    handleRemove (file, fileList) {
      let delPics = this.mangePics(fileList)
      this.form.fileArr = delPics
      this.form.fileUrl = delPics[0] ? delPics[0].url : ''
      // console.log(this.form.fileArr)
    },
    mangePics (fileList) {
      let _imgArr = []
      fileList.forEach((v, index) => {
        let imgUrl = {}
        if (fileList[index].response) {
          imgUrl.url = fileList[index].response.result
        } else {
          imgUrl.url = fileList[index].url
        }
        index === 0 ? imgUrl.is_master = true : imgUrl.is_master = false
        _imgArr.push(imgUrl)
      })
      return _imgArr
    },
    handlePictureCardPreview (file) {
      this.dialogImageUrl = file.url
      this.dialogVisible = true
    },
    addStandard () {
      this.form.standard.push({
        upc_id: '',
        upc_unit: '',
        upc_unit_num: ''
      })
    },
    // get category
    getUpcCategory (deepth, parentId) {
      let params = {
        depth: deepth,
        parent_id: parentId || ''
      }
      getUpcCategory(params).then((res) => {
        let _options = `options${deepth}`
        if (res.data.result) {
          this[_options] = res.data.result
        }
      })
    },
    handleSelect (val) {
      this.form.value2 = ''
      this.form.value3 = ''
      this.getUpcCategory(2, val)
    },
    handleSelectTwo (val) {
      this.form.value3 = ''
      this.getUpcCategory(3, val)
    }
  }
}
</script>

<style scoped lang="scss">
  .num{width: 54px;}
  .standard{width: 94px;}
  .barcard{width: 380px;}
  .add-standard{margin: 10px 0 30px 80px;}
  table thead td,
  table tr td{padding-right: 30px;}
  .el-icon-delete{cursor: pointer;}
  .category{display: inline-block;}
  .tipText{color: #5a5e66;}
</style>



