<template>
  <!--æƒé™é›†åˆ-->
  <div class="role-manage">
    <el-button
            type="primary"
            @click="showDialog('create')"
            icon="el-icon-plus">
      <span>æ–°å»ºæƒé™é›†åˆ</span>
    </el-button>
    <el-table
            :data="tableData"
            style="width: 100%">
      <el-table-column
              prop="set_id"
              label="NO.">
      </el-table-column>
      <el-table-column
              prop="set_name"
              label="æƒé™é›†åˆåç§°">
      </el-table-column>
      <el-table-column
              prop="content"
              label="æè¿°">
      </el-table-column>
      <el-table-column
              label="æ“ä½œ">
        <template slot-scope="scope">
          <el-button
                  @click.native.prevent="showDialog('edit', scope.row)"
                  type="text"
                  size="small">
            ç¼–è¾‘
          </el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
            @size-change="handleSizeChange"
            @current-change="changePage"
            :current-page.sync="page"
            :page-sizes="[20, 50, 100, 200]"
            :page-size="pageSize"
            layout="total, sizes, prev, pager, next"
            prev-text="ä¸Šä¸€é¡µ"
            next-text="ä¸‹ä¸€é¡µ"
            :total="totalCount">
    </el-pagination>
    <el-dialog
            :title="dialogTitle"
            :visible.sync="show"
            width="30%">
      <el-form
              ref="createPermissionSet"
              label-width="110px"
              :model="formParams"
              :rules="rules">
        <el-form-item
                class="is-required"
                label="æƒé™é›†åˆåç§°"
                prop="permissionSetName">
          <el-input
                  v-model="formParams.permissionSetName">
          </el-input>
        </el-form-item>
        <el-form-item label="æè¿°">
          <el-input
                  v-model="formParams.permissionSetContent">
          </el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button
                @click="show = false">
          å– æ¶ˆ
        </el-button>
        <el-button
                v-show="dialogTitle === 'æ–°å»ºæƒé™é›†åˆ'"
                type="primary"
                @click="save('create')">
          ç¡® å®š
        </el-button>
        <el-button
                v-show="dialogTitle === 'ç¼–è¾‘æƒé™é›†åˆ'"
                type="primary"
                @click="save('edit')">
          ç¡® å®š
        </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
  import { getPermissionSetList, createPermissionSet, updatePermissionSet } from '../../api/api'

  export default {
    data () {
      return {
        // tableæ‰€æœ‰æ•°æ®
        tableData: [],
        // å½“å‰ç¿»é¡µæ•°
        page: 1,
        // æ€»é¡µæ•°
        totalPage: 3,
        // tableæ€»æ¡æ•°
        totalCount: 1,
        // æ¯é¡µæ˜¾ç¤ºæ¡æ•°
        pageSize: 20,
        show: false,
        // æ“ä½œï¼ˆæ–°å»º or ç¼–è¾‘ï¼‰
        operation: '',
        // å¼¹å‡ºæ¡†æ ‡é¢˜
        dialogTitle: '',
        formParams: {
          // æƒé™é›†åˆåç§°
          permissionSetName: '',
          // æè¿°ä¿¡æ¯
          permissionSetContent: '',
          // æƒé™é›†åˆid
          permissionSetId: '',
        },
        rules: {
          permissionSetName: [{
            required: true, message: 'æƒé™é›†åˆåç§°ä¸èƒ½ä¸ºç©º'
          }]
        }
      }
    },
    mounted () {
      this.getPermissionSetList(1, 20)
    },
    methods: {
      /**
       * èŽ·å–æƒé™åˆ—è¡¨
       * @param {Object} page å½“å‰é¡µ
       * @param {Object} page å½“å‰é¡µ
       */
      getPermissionSetList (page, pageSize) {
        this.page = page
        this.pageSize = pageSize
        let params = {
          set_name: '',
          page_size: pageSize,
          page_num: this.page
        }
        getPermissionSetList(params).then(res => {
          if (res.data.error_no === 0) {
            this.tableData = res.data.result.permission_sets
            this.totalCount = parseInt(res.data.result.total_count)
          } else {
            this.$message.error(res.data.error_msg)
          }
        })
          .catch(error => {
            this.$message.error('æœåŠ¡å™¨å‚²å¨‡äº†ï¼Œè¯·åˆ·æ–°é‡è¯•')
          })
      },
      changePage (val) {
        this.getPermissionSetList(val, this.pageSize)
      },
      handleSizeChange (val) {
        this.getPermissionSetList(1, val)
      },
      /**
       * æ–°å»ºã€ç¼–è¾‘æƒé™é›†åˆ
       * @param {String} operation æ“ä½œï¼ˆç¼–è¾‘ or åˆ é™¤ï¼‰
       * @param {Object} data
       */
      showDialog (operation, data = {}) {
        this.show = true
        if (operation ===  'create') {
          this.$nextTick(() => {
            this.$refs['createPermissionSet'].resetFields()
            this.formParams.permissionSetName = ''
            this.formParams.permissionSetContent = ''
            this.formParams.permissionSetId = ''
          })
          this.dialogTitle = 'æ–°å»ºæƒé™é›†åˆ'
        } else {
          this.formParams.permissionSetName = data.set_name || ''
          this.formParams.permissionSetContent = data.content || ''
          this.formParams.permissionSetId = data.set_id || ''
          this.dialogTitle = 'ç¼–è¾‘æƒé™é›†åˆ'
        }
      },
      /**
       * ä¿å­˜
       * @param {String} operation æ“ä½œï¼ˆç¼–è¾‘ or åˆ é™¤ï¼‰
       */
      save (operation) {
        this.formParams.permissionSetName = this.formParams.permissionSetName.trim()
        this.$refs['createPermissionSet'].validate((valid) => {
          if (valid) {
            let params = {
              set_name: this.formParams.permissionSetName.trim(),
              content: this.formParams.permissionSetContent.trim()
            }
            if (operation === 'create') {
              createPermissionSet(params).then(res => {
                if (res.data.error_no === 0) {
                  this.$message.success('ä¿å­˜æˆåŠŸ')
                  this.getPermissionSetList(1, this.pageSize)
                  this.show = false
                } else {
                  this.$message.error(res.data.error_msg)
                }
              })
                .catch(error => {
                  this.$message.error('æœåŠ¡å™¨å‚²å¨‡äº†ï¼Œè¯·åˆ·æ–°é‡è¯•')
                })
            } else {
              params.set_id = this.formParams.permissionSetId.trim()
              updatePermissionSet(params).then(res => {
                if (res.data.error_no === 0) {
                  this.$message.success('ä¿å­˜æˆåŠŸ')
                  this.getPermissionSetList(this.page, this.pageSize)
                  this.show = false
                } else {
                  this.$message.error(res.data.error_msg)
                }
              })
                .catch((error) => {
                  this.$message.error(res.data.error_msg)
                })
            }
          } else {
            return false
          }
        })
      }
    }
  }
</script>



