<template>
  <!--æ•°æ®å­—æ®µ-->
  <div class="role-manage">
    <el-button
            type="primary"
            @click.native.prevent="showDialog('create')"
            icon="el-icon-plus">
      æ–°å»ºèµ„æº
    </el-button>
    <el-table
            :data="tableData"
            style="width: 100%">
      <el-table-column
              prop="data_id"
              label="NO.">
      </el-table-column>
      <el-table-column
              prop="data_name"
              label="å­—æ®µåç§°">
      </el-table-column>
      <el-table-column
              prop="data_code"
              label="å­—æ®µç¼–ç ">
      </el-table-column>
      <el-table-column
              prop="router"
              label="è·¯ç”±">
      </el-table-column>
      <el-table-column
              prop="content"
              label="æè¿°">
      </el-table-column>
      <el-table-column
              label="æ“ä½œ">
        <template slot-scope="scope">
          <el-button
                  @click.native.prevent="showDialog('edit', scope.row)"
                  type="text"
                  size="small">
            ç¼–è¾‘
          </el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
            @size-change="handleSizeChange"
            @current-change="changePage"
            :current-page.sync="page"
            :page-sizes="[20, 50, 100, 200]"
            :page-size="pageSize"
            layout="total, sizes, prev, pager, next"
            prev-text="ä¸Šä¸€é¡µ"
            next-text="ä¸‹ä¸€é¡µ"
            :total="totalCount">
    </el-pagination>
    <el-dialog
            :title="dialogTitle"
            :visible.sync="show"
            width="30%">
      <el-form
              :rules="rules"
              ref="dataField"
              :model="formParams"
              label-width="100px">
        <el-form-item
                prop="dataFieldName"
                class="is-required"
                label="å­—æ®µåç§°">
          <el-input v-model="formParams.dataFieldName"></el-input>
        </el-form-item>
        <el-form-item
                prop="dataFieldCode"
                class="is-required"
                label="å­—æ®µç¼–ç ">
          <el-input v-model="formParams.dataFieldCode"></el-input>
        </el-form-item>
        <el-form-item
                prop="route"
                class="is-required"
                label="è·¯ç”±">
          <el-input v-model="formParams.route"></el-input>
        </el-form-item>
        <el-form-item label="æè¿°">
          <el-input v-model="formParams.dataFieldContent"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="show = false">å– æ¶ˆ</el-button>
        <el-button
                v-show="dialogTitle === 'æ–°å»ºæ•°æ®å­—æ®µ'"
                type="primary"
                @click="save('create')">
          ç¡® å®š
        </el-button>
        <el-button
                v-show="dialogTitle === 'ç¼–è¾‘æ•°æ®å­—æ®µ'"
                type="primary"
                @click="save('edit')">
          ç¡® å®š
        </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
  import { getDataFieldList, createDataField, updateDataField } from '../../api/api'

  export default {
    data () {
      return {
        // tableæ‰€æœ‰æ•°æ®
        tableData: [],
        // å½“å‰ç¿»é¡µæ•°
        page: 1,
        // æ€»é¡µæ•°
        totalPage: 3,
        // tableæ€»æ¡æ•°
        totalCount: 1,
        // æ¯é¡µæ˜¾ç¤ºæ¡æ•°
        pageSize: 20,
        show: false,
        dialogTitle: '',
        formParams: {
          // å­—æ®µid
          dataFieldId: '',
          // å­—æ®µåç§°
          dataFieldName: '',
          // å­—æ®µç¼–ç 
          dataFieldCode: '',
          // æè¿°
          dataFieldContent: '',
          // è·¯ç”±
          route: ''
        },
        rules: {
          dataFieldName: [
            { required: true, message: 'å­—æ®µåç§°ä¸èƒ½ä¸ºç©º' }
          ],
          // å­—æ®µç¼–ç 
          dataFieldCode: [
            { required: true, message: 'å­—æ®µç¼–ç ä¸èƒ½ä¸ºç©º' }
          ],
          route: [
            { required: true, message: 'è·¯ç”±ä¸èƒ½ä¸ºç©º' }
          ]
        }
      }
    },
    mounted () {
      this.getDataFieldList(1, 20)
    },
    methods: {
      /**
       * èŽ·å–æƒé™åˆ—è¡¨
       * @param {Number} page é¡µç 
       * @param {Number} pageSize æ¯é¡µæ˜¾ç¤ºæ¡æ•°
       */
      getDataFieldList (page, pageSize) {
        this.page = page
        this.pageSize = pageSize
        let params = {
          page_size: pageSize,
          page_num: page
        }
        getDataFieldList(params).then(res => {
          if (res.data.error_no === 0) {
            this.tableData = res.data.result.data_fields
            this.totalCount = parseInt(res.data.result.total_count)
          } else {
            this.$message.error(res.data.error_msg)
          }
        })
          .catch(error => {
            this.$message.error('æœåŠ¡å™¨å‚²å¨‡äº†ï¼Œè¯·åˆ·æ–°é‡è¯•')
          })
      },
      changePage (val) {
        this.getDataFieldList(val, this.pageSize)
      },
      handleSizeChange (val) {
        this.getDataFieldList(1, val)
      },
      /**
       * æ–°å»ºã€ç¼–è¾‘æ•°æ®å­—æ®µ
       * @param {String} operation æ“ä½œï¼ˆç¼–è¾‘ or åˆ é™¤ï¼‰
       * @param {Object} data
       */
      showDialog (operation, data = {}) {
        this.show = true
        if (operation ===  'create') {
          this.$nextTick(() => {
            this.$refs['dataField'].resetFields()
            this.formParams.dataFieldName = ''
            this.formParams.dataFieldContent = ''
            this.formParams.dataFieldId = ''
            this.formParams.dataFieldCode = ''
            this.formParams.route = ''
          })
          this.dialogTitle = 'æ–°å»ºæ•°æ®å­—æ®µ'
        } else {
          this.formParams.dataFieldName = data.data_name || ''
          this.formParams.dataFieldContent = data.content || ''
          this.formParams.dataFieldId = data.data_id || ''
          this.formParams.dataFieldCode = data.data_code || ''
          this.formParams.route = data.router || ''
          this.dialogTitle = 'ç¼–è¾‘æ•°æ®å­—æ®µ'
        }
      },
      /**
       * ä¿å­˜
       * @param {String} operation æ“ä½œï¼ˆç¼–è¾‘ or åˆ é™¤ï¼‰
       */
      save (operation) {
        this.formParams.dataFieldName = this.formParams.dataFieldName.trim()
        this.formParams.dataFieldCode = this.formParams.dataFieldCode.trim()
        this.$refs['dataField'].validate((valid) => {
          if (valid) {
            let params = {
              data_name: this.formParams.dataFieldName,
              content: this.formParams.dataFieldContent,
              data_code: this.formParams.dataFieldCode,
              router: this.formParams.route
            }
            if (operation === 'create') {
              createDataField(params).then(res => {
                if (res.data.error_no === 0) {
                  this.$message.success('ä¿å­˜æˆåŠŸ')
                  this.getDataFieldList(1, this.pageSize)
                  this.show = false
                } else {
                  this.$message.error(res.data.error_msg)
                }
              })
                .catch(error => {
                  this.$message.error('æœåŠ¡å™¨å‚²å¨‡äº†ï¼Œè¯·åˆ·æ–°é‡è¯•')
                })
            } else {
              params.data_id = this.formParams.dataFieldId
              updateDataField(params).then(res => {
                if (res.data.error_no === 0) {
                  this.$message.success('ä¿å­˜æˆåŠŸ')
                  this.getDataFieldList(this.page, this.pageSize)
                  this.show = false
                } else {
                  this.$message.error(res.data.error_msg)
                }
              })
                .catch((error) => {
                  this.$message.error('æœåŠ¡å™¨å‚²å¨‡äº†ï¼Œè¯·åˆ·æ–°é‡è¯•')
                })
            }
          }
        })
      }
    }
  }
</script>



