<template>
  <div class="role-edit">
    <el-form
      ref="form"
      :model="formData"
      label-width="100px"
      label-position="left">
      <el-form-item label="è§’è‰²åç§°">
        <el-input
          v-model="formData.role_name"
          :disabled="!!roleId"
          placeholder="è¯·è¾“å…¥è§’è‰²åç§°">
        </el-input>
      </el-form-item>
      <template v-if="roleId">
        <el-form-item label="å·²å…³è”æƒé™">
          <template v-if="rolePermission.length == 0">
            <p class="permission-has">æ— </p>
          </template>
          <template v-else>
            <template v-for="item in rolePermission">
              <p class="permission-has">{{item}}</p>
            </template>
          </template>
        </el-form-item>
      </template>
      <el-form-item label="å¯å…³è”æƒé™">
        <template v-for="(perSet, index) in permissionList">
          <check-all-permissions
            :checkedDataFromProps.sync="formData.permission_ids[index]"
            :allRenderData="perSet"
            :allCheckData="permissionListNest[index]">
          </check-all-permissions>
        </template>
      </el-form-item>
      <el-form-item>
        <el-button
          type="primary"
          @click="onSubmit">
          ä¿å­˜
        </el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import { getPermissionPage, getRolePermissions, modifyRolePermissions, createRole } from '../api/api'
import checkAllPermissions from '../components/checkAllPermissions'

export default {
  components: {
    'check-all-permissions': checkAllPermissions
  },
  data () {
    return {
      // ç¼–è¾‘æ—¶çš„è§’è‰²id
      roleId: this.$route.query.role_id ? this.$route.query.role_id : '',
      // æäº¤çš„post data
      formData: {
        role_name: '',
        permission_ids: [],
        role_id: this.roleId
      },
      // æ‰€æœ‰æƒé™æ¸²æŸ“æ•°æ®
      permissionList: [],
      // æ‰€æœ‰æƒé™idæ•°æ®ï¼Œåˆ‡å‰²ä¸ºäºŒç»´æ•°ç»„ï¼Œæ–¹ä¾¿åšcheck-all
      permissionListNest: [],
      // å½“å‰è§’è‰²çš„æƒé™
      rolePermission: []
    }
  },
  methods: {
    // èŽ·å–å½“å‰è§’è‰²ç»‘å®šçš„æƒé™
    getRolePermission () {
      const getParams = {
        role_id: this.roleId
      }
      getRolePermissions(getParams).then(response => {
        const res = response.data
        if (res.error_no === 0) {
          if (res.result.role_info) {
            this.formData.role_name = res.result.role_info.role_name
            const permissionRes = res.result.role_permissions
            this.permissionList = permissionRes
            // è·Ÿä¸Šè¾¹æ¸²æŸ“æ‰€æœ‰æ•°æ®é€»è¾‘åŒç†ï¼Œå‘formData.permission_idsäºŒç»´æ•°ç»„é‡Œpushæ•°æ®ï¼ŒåŒæ—¶ç”Ÿæˆæ‰€æœ‰å·²ç»æ·»åŠ çš„æƒé™åç§°
            for (let i = 0; i < permissionRes.length; i++) {
              this.formData.permission_ids[i] = []
              this.permissionListNest[i] = []
              for (let j = 0; j < permissionRes[i].permissions.length; j++) {
                this.permissionListNest[i].push(this.permissionList[i].permissions[j].permission_id)
                if (permissionRes[i].permissions[j].granted == 1) {
                  this.formData.permission_ids[i].push(permissionRes[i].permissions[j].permission_id)
                  this.rolePermission.push(permissionRes[i].permissions[j].permission_name)
                }
              }
            }
          }
        } else {
          this.$message.error(res.error_msg)
        }
      })
      .catch(error => {
        this.$message.error('æœåŠ¡å™¨å‚²å¨‡äº†ï¼Œè¯·åˆ·æ–°é‡è¯•')
      })
    },
    // æäº¤post
    onSubmit () {
      // å¯¹formData.permission_idsåšæ•°ç»„concatï¼Œæ–¹ä¾¿join
      let permissionIds = []
      for (let i = 0; i < this.formData.permission_ids.length; i++) {
        permissionIds = permissionIds.concat(this.formData.permission_ids[i])
      }
      // åˆ¤æ–­æ˜¯ç¼–è¾‘è¿˜æ˜¯æ–°å»º
      if (this.roleId) {
        const postData = {
          role_id: this.roleId,
          permission_ids: permissionIds.join(',')
        }
        modifyRolePermissions(postData).then(response => {
          const res = response.data
          if (res.error_no === 0) {
            this.$message.success('ä¿®æ”¹æˆåŠŸ!')
            this.$router.push({ path: '/privilegeManage?tab=roleList'})
          } else {
            this.$message.error(res.error_msg)
          }
        })
        .catch(error => {
          this.$message.error('æœåŠ¡å™¨å‚²å¨‡äº†ï¼Œè¯·åˆ·æ–°é‡è¯•')
        })
      } else {
        const postData = {
          role_name: this.formData.role_name,
          permission_ids: permissionIds.join(',')
        }
        createRole(postData).then(response => {
          const res = response.data
          if (res.error_no === 0) {
            this.$message.success('æ–°å»ºæˆåŠŸ!')
            this.$router.push({ path: '/privilegeManage?tab=roleList'})
          } else {
            this.$message.error(res.error_msg)
          }
        })
        .catch(error => {
          this.$message.error('æœåŠ¡å™¨å‚²å¨‡äº†ï¼Œè¯·åˆ·æ–°é‡è¯•')
        })
      }
    }
  },
  created () {
    this.getRolePermission()
  }
}
</script>

<style lang="scss">
  .role-edit{

    .el-form{
      background-color: #fff;
      padding: 20px;

      .el-form-item__content{
        width: auto;

        .el-input{
          width: 380px;
          margin-right: 15px;
        }

        .permission-has{
          color: #5a5e66;
          margin: 0;
        }
      }
    }
  }
</style>



