<template>
  <!--æ•°æ®å­—æ®µ-->
  <div class="role-manage">
    <el-button
            type="primary"
            @click="showDialog('create')"
            icon="el-icon-plus">
      æ–°å»ºé¡µé¢å…ƒç´ 
    </el-button>
    <el-table
            :data="tableData"
            style="width: 100%">
      <el-table-column
              prop="page_id"
              label="NO.">
      </el-table-column>
      <el-table-column
              prop="page_name"
              label="å…ƒç´ åç§°">
      </el-table-column>
      <el-table-column
              prop="page_code"
              label="å…ƒç´ ç¼–ç ">
      </el-table-column>
      <el-table-column
              prop="content"
              label="æè¿°">
      </el-table-column>
      <el-table-column
              label="æ“ä½œ">
        <template slot-scope="scope">
          <el-button
                  @click.native.prevent="showDialog('edit', scope.row)"
                  type="text"
                  size="small">
            ç¼–è¾‘
          </el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
            @size-change="handleSizeChange"
            @current-change="changePage"
            :current-page.sync="page"
            :page-sizes="[20, 50, 100, 200]"
            :page-size="pageSize"
            layout="total, sizes, prev, pager, next"
            prev-text="ä¸Šä¸€é¡µ"
            next-text="ä¸‹ä¸€é¡µ"
            :total="totalCount">
    </el-pagination>
    <el-dialog
            :title="dialogTitle"
            :visible.sync="show"
            width="30%">
      <el-form
              :model="formParams"
              :rules="rules"
              ref="pageItem"
              label-width="100px">
        <el-form-item
                prop="pageItemName"
                class="is-required"
                label="å…ƒç´ åç§°">
          <el-input v-model="formParams.pageItemName"></el-input>
        </el-form-item>
        <el-form-item
                prop="pageItemCode"
                class="is-required"
                label="å…ƒç´ ç¼–ç ">
          <el-input v-model="formParams.pageItemCode"></el-input>
        </el-form-item>
        <el-form-item
                label="æè¿°">
          <el-input v-model="formParams.pageItemContent"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="show = false">å– æ¶ˆ</el-button>
        <el-button
                v-show="dialogTitle === 'æ–°å»ºé¡µé¢å…ƒç´ '"
                type="primary"
                @click="save('create')">
          ç¡® å®š
        </el-button>
        <el-button
                v-show="dialogTitle === 'ç¼–è¾‘é¡µé¢å…ƒç´ '"
                type="primary"
                @click="save('edit')">
          ç¡® å®š
        </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
  import { getPageItemList, createPageItem, updatePageItem } from '../../api/api'
  const APP_ID = 1

  export default {
    data () {
      return {
        // tableæ‰€æœ‰æ•°æ®
        tableData: [],
        // å½“å‰ç¿»é¡µæ•°
        page: 1,
        // æ€»é¡µæ•°
        totalPage: 3,
        // tableæ€»æ¡æ•°
        totalCount: 1,
        // æ¯é¡µæ˜¾ç¤ºå¤šå°‘æ¡
        pageSize: 20,
        show: false,
        dialogTitle: '',
        formParams: {
          // å…ƒç´ id
          pageItemId: '',
          // å…ƒç´ åç§°
          pageItemName: '',
          // å…ƒç´ ç¼–ç 
          pageItemCode: '',
          // æè¿°
          pageItemContent: '',
        },
        rules: {
          pageItemName: [{
            required: true, message: 'å…ƒç´ åç§°ä¸èƒ½ä¸ºç©º'
          }],
          pageItemCode: [{
            required: true, message: 'å…ƒç´ ç¼–ç ä¸èƒ½ä¸ºç©º'
          }]
        }
      }
    },
    mounted () {
      this.getPageItemList(1, 20)
    },
    methods: {
      /**
       * èŽ·å–é¡µé¢å…ƒç´ 
       * @param {Number} page é¡µç 
       * @param {Number} pageSize æ¯é¡µæ˜¾ç¤ºçš„æ¡æ•°
       */
      getPageItemList (page, pageSize) {
        this.page = page
        this.pageSize = pageSize
        let params = {
          page_size: pageSize,
          page_num: page
        }
        getPageItemList(params).then(res => {
          if (res.data.error_no === 0) {
            this.tableData = res.data.result.page_items
            this.totalCount = parseInt(res.data.result.total_count)
          } else {
            this.$message.error(res.data.error_msg)
          }
        })
          .catch(error => {
            this.$message.error('æœåŠ¡å™¨å‚²å¨‡äº†ï¼Œè¯·åˆ·æ–°é‡è¯•')
          })
      },
      changePage (val) {
        this.getPageItemList(val, this.pageSize)
      },
      handleSizeChange (val) {
        this.getPageItemList(1, val)
      },
      /**
       * æ–°å»ºã€ç¼–è¾‘é¡µé¢å…ƒç´ 
       * @param {String} operation æ“ä½œï¼ˆç¼–è¾‘ or åˆ é™¤ï¼‰
       * @param {Object} data
       */
      showDialog (operation, data = {}) {
        this.show = true
        if (operation ===  'create') {
          this.$nextTick(() => {
            this.$refs['pageItem'].resetFields()
            this.formParams.pageItemName = ''
            this.formParams.pageItemCode = ''
            this.formParams.pageItemId = ''
            this.formParams.pageItemContent = ''
          })
          this.dialogTitle = 'æ–°å»ºé¡µé¢å…ƒç´ '
        } else {
          this.formParams.pageItemName = data.page_name || ''
          this.formParams.pageItemCode = data.page_code || ''
          this.formParams.pageItemId = data.page_id || ''
          this.formParams.pageItemContent = data.content || ''
          this.dialogTitle = 'ç¼–è¾‘é¡µé¢å…ƒç´ '
        }
      },
      /**
       * ä¿å­˜
       * @param {String} operation æ“ä½œï¼ˆç¼–è¾‘ or åˆ é™¤ï¼‰
       */
      save (operation) {
        this.formParams.pageItemName = this.formParams.pageItemName.trim()
        this.formParams.pageItemCode = this.formParams.pageItemCode.trim()
        this.$refs['pageItem'].validate((valid) => {
          if (valid) {
            let params = {
              page_name: this.formParams.pageItemName,
              content: this.formParams.pageItemContent,
              page_code: this.formParams.pageItemCode
            }
            if (operation === 'create') {
              createPageItem(params).then(res => {
                if (res.data.error_no === 0) {
                  this.$message.success('ä¿å­˜æˆåŠŸ')
                  this.getPageItemList(1, this.pageSize)
                  this.show = false
                } else {
                  this.$message.error(res.data.error_msg)
                }
              })
                .catch(error => {
                  this.$message.error('æœåŠ¡å™¨å‚²å¨‡äº†ï¼Œè¯·åˆ·æ–°é‡è¯•')
                })
            } else {
              params.page_id = this.formParams.pageItemId
              updatePageItem(params).then(res => {
                if (res.data.error_no === 0) {
                  this.$message.success('ä¿å­˜æˆåŠŸ')
                  this.getPageItemList(this.page, this.pageSize)
                  this.show = false
                } else {
                  this.$message.error(res.data.error_msg)
                }
              })
                .catch((error) => {
                  this.$message.error('æœåŠ¡å™¨å‚²å¨‡äº†ï¼Œè¯·åˆ·æ–°é‡è¯•')
                })
            }
          }
        })
      }
    }
  }
</script>



