      // è¿™é‡Œçš„totalæ˜¯ç”¨ä½œæ•´è¡¨é‡‘é¢ç»Ÿè®¡çš„ï¼ˆå’ŒpurchaseTableä¸­çš„ä¸ä¸€æ ·ï¼‰
