import {
  getArrivalList,
  searchVendor,
  getcitylist,
  createRepostTask
 } from '../../../../api/api'
import util from '../../../../common/js/util'
import citywarehouse from '@/components/citywarehouse/citywarehouse.vue'

export default {
  data () {
    return {
      btn: {
        getarrivalorderbyid: util.jdugeShow('order-arrival-getarrivalorderbyid'),
        orderexport: util.jdugeShow('arrival-order-export'), // å¯¼å‡º
        exportread: util.jdugeShow('export-read') // å¯¼å‡ºæŸ¥çœ‹
      },
      isSupplier: util.isSupplier(),
      isExport: false,
      params: {},
      citys: [],
      city_name: '',
      filter: {
        arrival_id: '',
        vendor_name: '',
        vendorId: '',
        po_contact_name: '',
        actual_time_start: '',
        actual_time_end: ''
      },
      // åˆ°è´§å•åˆ—è¡¨å±•ç¤º
      list: {
        arrilvalTabel: [],
        currentPage: 1,
        currentPageSize: 20,
        total: 0
      }
    }
  },
  created () {
    this.getArrivalList()
    getcitylist().then((res) => {
      if (res && res.data && res.data.error_no === 0) {
        this.citys = (res.data.result && res.data.result.city_list) || []
      }
    })
  },
  watch: {
    'filter.vendor_name': function (val) {
      if (!val) {
        this.filter.vendorId = ''
      }
    }
  },
  methods: {
    querySupplier (queryString, callback) {
      let params = {}
      params.vendor_name = queryString
      searchVendor(params).then((res) => {
        let vendorArr = []
        let result = (Array.isArray(res.data.result) && res.data.result) || []
        result.forEach((val, index, arr) => {
          vendorArr[index] = {}
          vendorArr[index].value = val.vendor_name
          vendorArr[index].vendor_id = val.vendor_id
        })
        callback(vendorArr)
      })
    },
    handleSelect (item) {
      this.filter.vendorId = item.vendor_id
    },
    search () {
      let params = {}
      if (this.filter.arrival_id) {
        params.arrival_id = this.filter.arrival_id
      }
      if (this.filter.po_id) {
        params.po_id = this.filter.po_id
      }
      if (this.filter.vendor_name) {
        params.vendor_name = this.filter.vendor_name
      }
      if (this.filter.po_contact_name) {
        params.po_contact_name = this.filter.po_contact_name
      }
      if (this.city_name) {
        params.city_name = this.city_name
      }
      if (this.filter.actual_time_start && this.filter.actual_time_end) {
        params.actual_time_start = parseInt(util.getTimeStamp(this.filter.actual_time_start))
        params.actual_time_end = parseInt(util.getTimeStamp(this.filter.actual_time_end))
      } else if ((this.filter.actual_time_start && !this.filter.actual_time_end) || (!this.filter.actual_time_start && this.filter.actual_time_end)) {
        this.$message.error('è¯·å¡«å†™æ—¶é—´èŒƒå›´ï¼ŒåŒ…æ‹¬èµ·å§‹æ—¶é—´å’Œç»“æŸæ—¶é—´')
      }
      // todo: æ•°æ®åˆ°åº•æ€Žä¹ˆä¼ ï¼Œåœ¨è€ƒè™‘
      params.page_size = this.list.currentPageSize
      this.getArrivalList(params)
    },
    exportResult () {
      this.$confirm('æ˜¯å¦è¦å¯¼å‡ºå½“å‰æ‰€æœ‰æŸ¥è¯¢ç»“æžœ?', '', {
        confirmButtonText: 'ç¡®å®š',
        cancelButtonText: 'å–æ¶ˆ'
      }).then(() => {
        let params = {}
        params.task_type = 11
        params.app_id = 2
        params.query_parameters = this.params
        if (this.filter.vendorId) {
          params.query_parameters.vendor_id = this.filter.vendorId
          delete params.query_parameters.vendor_name
        }
        if (this.params !== {}) {
          delete params.query_parameters.page_size
        }
        createRepostTask(params).then((res) => {
          if (res && res.data && res.data.error_no === 0) {
            this.$message.success('ä»»åŠ¡å·²åŠ å…¥å¯¼å‡ºä»»åŠ¡ç®¡ç†åˆ—è¡¨ï¼Œè¯·å‰å¾€æŸ¥çœ‹')
          } else {
            this.$message.error(res.data.error_msg || 'å¯¼å‡ºå¤±è´¥ï¼Œè¯·ç¨å€™é‡è¯•')
          }
        })
      }).catch(() => {})
    },

    getArrivalList (param) {
      let params = {}
      if (!param) {
        params.page_size = this.list.currentPageSize
      } else {
        params = param
      }
      getArrivalList(params).then((res) => {
        if (res.data.error_no === 0) {
          this.params = params
          this.list.arrilvalTabel = res.data && res.data.result && res.data.result.arrival_order_list
          this.list.total = res.data && res.data.result && res.data.result.total
        } else {
          this.$message.error(res.data.error_msg || 'èŽ·å–æ•°æ®å¤±è´¥')
        }
      })
    },
    jumpToPurchase (poId) {
      this.$router.push({path: '/purchaseinfo', query: {po_id: poId}})
    },
    jumpToSupplier (vendorId, vendorName) {
      this.$router.push({path: '/createBasicInfo', query: {create: vendorId, name: vendorName}})
    },
    handleCurrentChange (val) {
      this.list.currentPage = val
      this.params.page_num = val
      this.getArrivalList(this.params)
    },
    handleSizeChange (val) {
      this.list.currentPageSize = val
      this.params.page_size = val
      this.getArrivalList(this.params)
    }
  },
  components: {
    'city-warehouse': citywarehouse
  }
}



