<template>
  <div class="user-edit">
    <el-form
            ref="form"
            :model="formData"
            label-width="100px"
            label-position="left">
      <el-form-item
              label="ç”¨æˆ·ç±»åž‹">
        <el-select
                :disabled="!!userId || !isWms"
                @change="changeUserType"
                v-model="formData.userType">
          <el-option
                  label="sso"
                  value="sso">
          </el-option>
          <el-option
                  label="wmpass"
                  value="wmpass">
          </el-option>
        </el-select>
      </el-form-item>
      <!--å†…éƒ¨ç”¨æˆ·-->
      <div v-show="formData.userType === 'sso'">
        <el-form-item label="ç”¨æˆ·é‚®ç®±">
          <el-autocomplete
                  :disabled="!!userId"
                  v-model="formData.email"
                  :fetch-suggestions='queryUserMail'
                  :trigger-on-focus='false'
                  placeholder="è¯·è¾“å…¥ç”¨æˆ·é‚®ç®±"
                  @select="selectEmail"
                  valueKey="value"
                  value="info">
          </el-autocomplete>
        </el-form-item>
        <el-form-item
                v-show="formData.name != ''"
                label="å§“å">
          <el-input
                  :disabled="true"
                  v-model="formData.name">
          </el-input>
        </el-form-item>
        <el-form-item
                v-show="formData.phone != ''"
                label="æ‰‹æœºå·">
          <el-input
                  :disabled="true"
                  v-model="formData.phone">
          </el-input>
        </el-form-item>
      </div>
      <!--å¤–éƒ¨ç”¨æˆ·-->
      <div v-show="formData.userType === 'wmpass'">
        <el-form-item label="æ‰‹æœºå·">
          <el-input
                  :disabled="!!userId"
                  v-model="formData.phone">
          </el-input>
        </el-form-item>
        <el-form-item
                v-show="formData.name != ''"
                label="å§“å">
          <el-input
                  :disabled="true"
                  v-model="formData.name">
          </el-input>
        </el-form-item>
        <el-form-item
                v-show="formData.email != ''"
                label="é‚®ç®±">
          <el-input
                  :disabled="true"
                  v-model="formData.email">
          </el-input>
        </el-form-item>
      </div>
      <template v-if="userId">
        <el-form-item label="å·²å…³è”è§’è‰²">
          <div class="expandingArea">
            <span class="hidden-text">{{userRole.join(',')}}</span>
            <el-input
                    :disabled="true"
                    v-model="userRole.join(',')"
                    type="textarea">
            </el-input>
          </div>
        </el-form-item>
      </template>
      <el-form-item label="å¯å…³è”è§’è‰²">
        <el-checkbox-group v-model="formData.role_ids">
          <el-checkbox
                  v-for="item in roleData"
                  :label="item.role_id"
                  name="type"
                  :key="item.role_id"
                  v-if="item.enabled == 1">
            {{item.role_name}}
          </el-checkbox>
        </el-checkbox-group>
      </el-form-item>
      <el-form-item>
        <el-button
                type="primary"
                @click="onSubmit">
          ä¿å­˜
        </el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
  import { getRoleList, getUserRoles, modifyUserRoles, createUser, searchUserMail } from '../api/api'
  export default {
    data () {
      return {
        // ç¼–è¾‘æ—¶çš„ç”¨æˆ·id
        userId: this.$route.query.user_id ? this.$route.query.user_id : '',
        // æäº¤çš„post data
        formData: {
          email: '',
          role_ids: [],
          user_id: this.userId,
          // ç”¨æˆ·å
          name: '',
          // æ‰‹æœºå·ç 
          phone: '',
          // ç”¨æˆ·ç±»åž‹
          userType: 'sso' // 'SSO' å†…éƒ¨ç”¨æˆ·  'WMPASS'å¤–éƒ¨ç”¨æˆ·
        },
        // æ‰€æœ‰è§’è‰²æ•°æ®
        roleData: [],
        // å½“å‰ç”¨æˆ·çš„è§’è‰²
        userRole: [],
        // å½©äº‘ç³»ç»Ÿè¿˜æ˜¯æ²§æµ·ç³»ç»Ÿ
        isWms: window.location.href.indexOf('wms') >= 0
      }
    },
    methods: {
      // æŸ¥æ‰¾ç”¨æˆ·é‚®ç®±
      queryUserMail (queryString, callback) {
        let params = {}
        params.wd = queryString
        searchUserMail(params).then((res) => {
          let venderArr = []
          let result = (Array.isArray(res.data.result.list) && res.data.result.list) || []
          result.forEach((val, index, arr) => {
            venderArr[index] = {}
            // venderArr[index].value = (val.email + '').replace('@ele.me','')
            venderArr[index].value = val.email
            venderArr[index].info = JSON.stringify({name: val.name, mobile: val.mobile})
          })
          callback(venderArr)
        })
      },
      // èŽ·å–æ‰€æœ‰è§’è‰²æ•°æ®
      getRoleData () {
        const getParams = {
          // åŽç«¯æ²¡æœ‰å…¨é‡æŽ¥å£ï¼Œæš‚æ—¶è®¾ä¸ªå·¨å¤§å€¼
          page_size: 100000
        }
        getRoleList(getParams).then(response => {
          const res = response.data
          if (res.error_no === 0) {
            this.roleData = res.result.roles
          } else {
            this.$message.error(res.error_msg)
          }
        })
          .catch(error => {
            this.$message.error('æœåŠ¡å™¨å‚²å¨‡äº†ï¼Œè¯·åˆ·æ–°é‡è¯•')
          })
      },
      // èŽ·å–å½“å‰ç”¨æˆ·ç»‘å®šçš„è§’è‰²
      getUserRole () {
        const getParams = {
          user_id: this.userId
        }
        getUserRoles(getParams).then(response => {
          const res = response.data
          if (res.error_no === 0) {
            // åˆ¤æ–­æ˜¯å¦æœ‰ç”¨æˆ·ä¿¡æ¯
            if (res.result.user_info) {
              this.formData.email = res.result.user_info.email.replace('@ele.me', '')
              this.formData.userType = res.result.user_info.type
              this.formData.phone = res.result.user_info.phone
              this.formData.name = res.result.user_info.real_name
              let rolesRes = res.result.user_roles.granted
              for (let i = 0; i < rolesRes.length; i++) {
                this.formData.role_ids.push(rolesRes[i].role_id)
                this.userRole.push(rolesRes[i].role_name)
              }
            }
          } else {
            this.$message.error(res.error_msg)
          }
        })
          .catch(error => {
            this.$message.error('æœåŠ¡å™¨å‚²å¨‡äº†ï¼Œè¯·åˆ·æ–°é‡è¯•')
          })
      },
      // é€‰ä¸­é‚®ç®±æ—¶å€™å›žè°ƒ
      selectEmail (data) {
        data =JSON.parse(data.info)
        this.formData.name = data.name
        this.formData.phone = data.mobile
      },
      changeUserType () {
        this.formData.name = ''
        this.formData.phone = ''
        this.formData.email = ''
      },
      // æäº¤post
      onSubmit () {
        // åˆ¤æ–­æ˜¯æ–°å»ºè¿˜æ˜¯ç¼–è¾‘
        if (this.userId) {
          const postData = {
            user_id: this.userId,
            role_ids: this.formData.role_ids.join(',')
          }
          modifyUserRoles(postData).then(response => {
            const res = response.data
            if (res.error_no === 0) {
              this.$message.success('ä¿®æ”¹æˆåŠŸ!')
              this.$router.push({ path: '/privilegeManage'})
            } else {
              this.$message.error(res.error_msg)
            }
          })
            .catch(error => {
              this.$message.error('æœåŠ¡å™¨å‚²å¨‡äº†ï¼Œè¯·åˆ·æ–°é‡è¯•')
            })
        } else {
          const postData = {
            email: this.formData.email,
            role_ids: this.formData.role_ids.join(','),
            phone: this.formData.phone,
            type: this.formData.userType
          }
          createUser(postData).then(response => {
            const res = response.data
            if (res.error_no === 0) {
              this.$message.success('æ–°å»ºæˆåŠŸ!')
              this.$router.push({ path: '/privilegeManage'})
            } else {
              this.$message.error(res.error_msg)
            }
          })
            .catch(error => {
              this.$message.error('æœåŠ¡å™¨å‚²å¨‡äº†ï¼Œè¯·åˆ·æ–°é‡è¯•')
            })
        }
      }
    },
    mounted () {
      this.getRoleData()
      // åˆ¤æ–­æ˜¯æ–°å»ºè¿˜æ˜¯ç¼–è¾‘
      if (this.userId) {
        this.getUserRole()
      }
    }
  }
</script>

<style lang="scss">
  .user-edit{

    .el-form{
      background-color: #fff;
      padding: 20px;

      .el-form-item__content{
        width: auto;

        .el-input{
          width: 310px;
          margin-right: 15px;
        }

        .expandingArea{
          position: relative;
        }

        .el-checkbox{
          margin: 0 30px 0 0;
        }

        .el-textarea{
          width: 385px;
          position: absolute;
          top: 0;
          left: 0;
          height: 100%;

          textarea{
            height: 100%;
            min-height: 40px!important;
          }
        }

        .hidden-text{
          display: block;
          visibility: hidden;
          width: 385px;
          padding: 6px 16px;
          line-height: 1.5;
        }
      }
    }
  }
</style>




