import qs from 'qs'
import commonData from '@/common/js/commonData'
import { getVendorUpcList, getskulist, updateVendorupcStatus, createVendorUpc, getUpcsbySkuId } from '@/api/api'
export default {
  data () {
    return {
      activeIndex: '3',
      isActive: '',
      productNum: -1,
      dialogVisible: false,
      dialogSku: false,
      vendorId: this.$route.query.create,
      alert: 0,
      radio: 1,
      upcRadio: 1,
      upcCode: [],
      // æ”¹å˜æ ¼å¼çš„sku
      changeSku: '',
      uoc: '',
      active: '',
      filter: {
        supplyID: '',
        code: '',
        name: ''
      },
      btnText: [
        'ç¦ç”¨',
        'å¼€å¯'
      ],
      tableData: [],
      tableData2: [],
      tableData3: [],
      loading: false,
      pageData: {
        page: 1,
        total: 0,
        count: 100,
          // éœ€è¦åŽæœŸä¿®æ”¹ï¼ŒåŽ»æŽ‰åˆ†é¡µå¿…é€‰é¡¹
        page_num: 0
      }
    }
  },
  created () {
    this.getvendorupclist()
  },
  methods: {
    getvendorupclist () {
      let params = {
        page_size: this.pageData.count,
        // page_num: this.pageData.page,
        vendor_id: this.vendorId
        // is_active: this.isActive
      }
      getVendorUpcList(params).then((res) => {
        // console.log(res.data.result.upcs);
        if (res.data.error_no === 0 && res.data.result.upcs && res.data.result.upcs.length !== 0) {
          this.tableData = this.regulateList(res.data.result.upcs)
        }
        // this.pageData.total = res.data.result.total
      })
    },
    // æŸ¥è¯¢å•†å“æ± 
    getProductSku () {
      if (!this.filter.name && !this.filter.supplyID && !this.filter.code) {
        this.$message({message: 'è¯·è¾“å…¥æŸ¥è¯¢æ¡ä»¶', type: 'swarning'})
        this.tableData2 = []
        this.productNum = -1
      } else {
        let params = {
          sku_name: this.filter.name,
          sku_id: this.filter.supplyID,
          upc_id: this.filter.code,
          page_size: 20
        }
        // console.log(params)
        getskulist(params).then((res) => {
          // console.log(res);
          if (res.data.error_no === 0) {
            if (res.data.result.skus.length === 0) {
              this.tableData2 = []
              this.productNum = -1
              this.$message({message: 'æ²¡æœ‰ç¬¦åˆæŸ¥è¯¢æ¡ä»¶çš„å•†å“', type: 'error'})
            } else if ((res.data.result.skus.length === 1) && res.data.result.skus[0].is_active === 2) {
              this.tableData2 = []
              this.productNum = -1
              this.$message({message: 'è¯¥å•†å“å·²è¢«ç¦ç”¨', type: 'error'})
            } else {
              res.data.result.skus.forEach((item, index) => {
                item.upcs.forEach((li, index) => {
                  if (li.is_default === 1) {
                    item.upc_id = li.upc_id
                    item.display_upc_id = li.display_upc_id
                    item.text = li.upc_unit_text
                    item.num = '1*' + li.upc_unit_num
                  }
                })
              })
              this.tableData2 = this.regulateList(res.data.result.skus)
              this.productNum = 1
            }
          } else {
            this.$message({message: res.data.error_msg, type: 'error'})
          }
        })
      }
    },
    // ç¡®å®šæ›´æ”¹è§„æ ¼
    changeUpc () {
      this.createVendorUpc(this.changeSku, this.radio)
    },
// å‘å•†å“æ± å¢žåŠ å•†å“
    createVendorUpc (sku, upc) {
      // console.log(upc);
      let params = {
        vendor_id: this.vendorId,
        sku_id: sku || this.changeSku,
        upc_id: upc
      }
      createVendorUpc(qs.stringify(params)).then((res) => {
        const status = res.data.error_no
        if (status === 0) {
          if (this.dialogSku) {
            this.$message({message: 'ä¿®æ”¹æˆåŠŸ', type: 'success'})
            this.getvendorupclist()
            this.dialogSku = false
          } else {
            this.$message({message: 'æˆåŠŸæ·»åŠ ', type: 'success'})
            this.getvendorupclist()
          }
        } else {
          this.productNum = -1
          this.$message({message: res.data.error_msg, type: 'swarning'})
        }
      })
    },
    regulateList (arr) {
      arr.forEach((item, index, array) => {
        if (item.sku_category_text_1) {
          item.sku_category_text = item.sku_category_text_1 + '>' + item.sku_category_text_2 + '>' + item.sku_category_text_3
        }
        if (item.sku_category_text) {
          item.sku_category_text = item.sku_category_text.replace(/,/g, ' > ')
        }
        if (item.upc_unit_num) {
          item.num = '1*' + item.upc_unit_num
        }
        // è½¬æ¢å•†å“åŒ…è£…åç§°
        // console.log(commonData)
        item.upc = commonData[item.upc_unit]
        if (item.sku_tax_rate) {
          switch (item.sku_tax_rate) {
            case 1 : item.sku = '17%'; break
            case 2 : item.sku = '11%'; break
            case 3 : item.sku = '6%'; break
            case 4 : item.sku = 0; break
          }
        }
        if (item.is_active) {
          item.state = item.is_active === 1 ? 'æ­£å¸¸é‡‡è´­' : 'æš‚åœé‡‡è´­'
        }
      })
      return arr
    },
    // æ›´æ”¹çŠ¶æ€
    changeStatus (upc, active) {
      this.upc = upc
      this.active = active
      this.dialogVisible = true
    },
// æ›´æ”¹çŠ¶æ€
    confirm () {
      let params = {
        vendor_id: this.vendorId,
        upc_id: this.upc,
        is_active: this.active === 1 ? 2 : 1
      }
      // console.log(params);
      updateVendorupcStatus(qs.stringify(params)).then((res) => {
        if (res.data.error_no === 0) {
          this.$message({message: 'ä¿®æ”¹æˆåŠŸ', type: 'success'})
          this.getvendorupclist()
        } else {
          this.$message({message: res.data.error_msg, type: 'swarning'})
        }
        this.dialogVisible = false
      })
    },
    // æŸ¥è¯¢è§„æ ¼
    getUpcsbySkuId (id, td) {
      this.changeSku = id
      this.radio = td
      let params = {
        sku_id: id
      }
      getUpcsbySkuId(params).then((res) => {
        if (res.data.error_no === 0) {
          // åˆ¤æ–­æ˜¯å¦æŸ¥è¯¢åˆ°è§„æ ¼åˆ—è¡¨æ˜¯å¦ä¸º0
          if (Array.isArray(res.data.result)) {
            this.tableData3 = this.regulateList(res.data.result)
            this.upcCode = this.tableData3
            this.dialogSku = true
          } else {
            this.$message({message: 'æ²¡æœ‰ä¿¡æ¯', type: 'swarning'})
          }
        } else {
          this.$message({message: res.data.error_msg, type: 'swarning'})
        }
      })
    }
  }
}
